//管理员相关路由

const adminrouter =[
    {
        //管理员列表
        path:'adminlist',
        name:'adminlist',
        meta:{index:1},
        component:()=>import('@views/adminlist/index')

      },
      {
        // 添加管理员
        path:'addadmin',
        name:'addadmin',
        meta:{index:2},
        component:()=>import('@views/addadmin')
      },
      {
        //添加新闻
        path:'addnews',
        name:'addnews',
        meta:{index:3},
        component:()=>import('@views/addnews')
      },
       //轮播图添加
       {
        path:'addbanner',
        name:"addbanner",
        meta:{index: 6},
        component:()=>import('@views/banner/index')
     },
     {
       path:'editbanner',
       name:"editbanner",
       meta:{index: 8},
       component:()=>import('@views/banner/index')
    },
]

export default  adminrouter


